body {
    background-image: url(https://catherineasquithgallery.com/uploads/posts/2021-03/1614839954_51-p-fon-dlya-shkolnogo-saita-76.jpg);
    background-attachment: fixed;
}

.wrapper {
    text-align: center;
    background-repeat: repeat;
    background-position: center;
}

.container {
    background-color: #ffffff;
    max-width: 550px;
    margin: 0 auto;
    border: 2px solid rgba(0, 0, 0, 0.291);
    
}

h1 {
    font-family: sans-serif;
    letter-spacing: 2px;
    font-size: 36px;
}

input {
    margin: 5px;
    padding: 5px;
    border: 2px solid #05cd51;
    border-radius: 5px;
}
input::placeholder {
	color: gray;
	font-style: italic;
  }



select {
    border: 2px solid #05cd51;
    border-radius: 3px;
	font-size: 14px;
	font-weight: normal;
	text-decoration: none;
	color: #fff;
	background-color: #05cd51;
    margin: 5px;
    padding: 5px;
}
table {
	max-width: 50%;
	margin: 10px auto;
	font-size: 14px;
    border-collapse: collapse;
}
td, th {
	padding: 7px;

}
th {
    font-size: 16px;
}
thead {
    background-color: #a0e899;
}


