.btn {
    text-align: center;
	display: inline-block;	
	box-sizing: border-box;
	padding:  7px 9px;
	margin: 5px 5px;
	outline: none;
	border: none;    
	border-radius: 3px;
	font-size: 14px; 
	font-weight: normal;
	text-decoration: none;
	color: #fff;
	background-color: #05cd51;
	cursor: pointer;
	user-select: none;
	appearance: none;
	touch-action: manipulation;
	transition: box-shadow .18s ease-out, background .18s ease-out,color .18s ease-out;
}
.btn:focus-visible {
	box-shadow: 0 0 0 3px lightskyblue;
}
.btn:hover {
	box-shadow: 0 1px 1px 0  #cfcfcf, 0 2px 5px 0  #cfcfcf;
}
.btn:active {
	background-color: #058c38 !important;
}
.btn:disabled {
	background-color: #aed2bc;
	color: #444;
	pointer-events: none;
}
.del {
    background-color: #ff0202;
}
.align {
    text-align: left;
}
.statisticWrapper {
	margin-top: 30px;
	margin-bottom: 10px;
}

.period {
	display: flex;
	justify-content: center;
	margin-top: 10px;
}

.active {
	color: #444;
}

.result {
	margin-top: 30px;
}


